/* наша клиенты */
.client
{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  
}

.client__list {
  display: flex;
  flex-wrap: wrap;
  margin-right: -30px;
  margin-bottom: -30px;
  margin-top: 30px;
  width: 100%;
  
 
}
.client__item {
  position: relative;
  
  width: calc((100% - 4 * 15px) /2);
  margin-right: 30px;
  margin-bottom: 30px;
  
&:not(:last-child){
  margin-bottom: 30px;
  
  
}
  @media (min-width: $tablet-width) {
    width: calc((100% - 6 * 15px) /3);
    &:not(:last-child) {
      margin-bottom: 30px;
      margin-right: 30px;
    }
   
  }
  
  @media (min-width: $laptop-width) {
    width: calc((100% - 12 * 15px) /6);
    &:not(:last-child) {
      margin-bottom: 30px;
      margin-right: 30px;
    }
    
  }
  // width: calc(100% / 6 - 30px);
  // margin-left: 30px;
  height: 92px;
}

.client__link {
  width: 100%;
  height: 100%;
 @include centerFlex();
  border: solid 1px;
  border-color: var(--social-color-primary);
  border-radius: 3px;
  transition-property: border-color;
  @extend %trans;
  &:hover,
  &:focus {
    border-color: #2196f3;
  }
}

.client__icon {
  width: 106px;
  height: 60px;
  @extend %trans;
  transition-property: fill;
  fill: var(--social-color-primary);
  .client__item:hover &,
  .client__item:focus & {
    fill: #2196f3;
  }
}
